* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    padding: 20px;
}

section {
    margin-bottom: 30px;
}

h1,
h2 {
    margin-bottom: 20px;
}

button {
    border: none;
    border-radius: 6px;
    background-color: blueviolet;
    color: white;
    font-size: 16px;
    padding: 10px;
}

button:hover,
button:focus {
    color: black;
    background-color: rgb(148, 154, 220);
    cursor: pointer;
}
